// app/projects/page.tsx
'use client';
import { useState } from 'react';
import { PROJECTS } from '../_utils/constants';
import HeroSection from '../components/HeroSection';
import ProjectCard from './components/ProjectCard';
import { motion, AnimatePresence } from 'framer-motion';
import Image from 'next/image';
import { XMarkIcon } from '@heroicons/react/24/outline';

export default function ProjectsPage() {
  const [selectedProject, setSelectedProject] = useState<typeof PROJECTS[0] | null>(null);
  const [activeImageIndex, setActiveImageIndex] = useState(0);

  const openProject = (project: typeof PROJECTS[0]) => {
    setSelectedProject(project);
    setActiveImageIndex(0);
    document.body.style.overflow = 'hidden';
  };

  const closeProject = () => {
    setSelectedProject(null);
    document.body.style.overflow = 'auto';
  };

  return (
    <div className="bg-gray-50">
      <HeroSection 
        title="Our Engineering Legacy"
        subtitle="Transforming landscapes across East Africa"
        variant="projects"
      />
      
      <section className="py-16 px-4">
        <div className="max-w-7xl mx-auto">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="text-center mb-16"
          >
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Featured Projects Portfolio
            </h2>
            <div className="w-24 h-1 bg-blue-900 mx-auto mb-6"></div>
            <p className="text-lg text-gray-600 max-w-3xl mx-auto">
              Discover our landmark projects that showcase our expertise in delivering complex infrastructure solutions
            </p>
          </motion.div>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {PROJECTS.map((project) => (
              <ProjectCard
                key={project.id}
                project={project}
                onClick={() => openProject(project)}
              />
            ))}
          </div>
        </div>
      </section>

      {/* Project Details Modal */}
      <AnimatePresence>
        {selectedProject && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-50 overflow-y-auto bg-black bg-opacity-75"
          >
            <div className="min-h-screen px-4 flex items-center justify-center">
              <div 
                className="fixed inset-0"
                onClick={closeProject}
              ></div>
              
              <motion.div
                initial={{ scale: 0.9, y: 50 }}
                animate={{ scale: 1, y: 0 }}
                exit={{ scale: 0.9, opacity: 0 }}
                className="relative bg-white rounded-xl max-w-6xl w-full max-h-[90vh] overflow-hidden"
              >
                <button
                  onClick={closeProject}
                  className="absolute top-4 right-4 z-10 bg-white rounded-full p-2 shadow-lg hover:bg-gray-100"
                >
                  <XMarkIcon className="h-6 w-6 text-gray-700" />
                </button>
                
                <div className="flex flex-col lg:flex-row h-[90vh]">
                  {/* Image Gallery */}
                  <div className="lg:w-7/12 relative">
                    <div className="relative h-96 lg:h-full">
                      <Image
                        src={selectedProject.images[activeImageIndex]}
                        alt={`${selectedProject.title} - Image ${activeImageIndex + 1}`}
                        fill
                        className="object-cover"
                        priority
                      />
                    </div>
                    
                    <div className="absolute bottom-4 left-4 flex space-x-2">
                      {selectedProject.images.map((_, index) => (
                        <button
                          key={index}
                          onClick={() => setActiveImageIndex(index)}
                          className={`w-3 h-3 rounded-full ${
                            index === activeImageIndex 
                              ? 'bg-blue-700' 
                              : 'bg-white'
                          }`}
                          aria-label={`View image ${index + 1}`}
                        />
                      ))}
                    </div>
                    
                    <div className="absolute bottom-4 right-4 bg-white p-2 rounded-lg shadow-md">
                      <span className="text-sm">
                        {activeImageIndex + 1} / {selectedProject.images.length}
                      </span>
                    </div>
                  </div>
                  
                  {/* Project Details */}
                  <div className="lg:w-5/12 p-6 lg:p-8 overflow-y-auto">
                    <div className="flex justify-between items-start mb-6">
                      <h2 className="text-2xl font-bold text-gray-900">
                        {selectedProject.title}
                      </h2>
                      <span className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm">
                        {selectedProject.year}
                      </span>
                    </div>
                    
                    <div className="flex items-center text-gray-600 mb-4">
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
                      </svg>
                      <span>{selectedProject.location}</span>
                    </div>
                    
                    <div className="mb-6">
                      <h3 className="text-lg font-semibold text-gray-900 mb-2">Project Value</h3>
                      <p className="text-xl font-bold text-blue-900">{selectedProject.value}</p>
                    </div>
                    
                    <div className="mb-8">
                      <h3 className="text-lg font-semibold text-gray-900 mb-3">Project Details</h3>
                      <p className="text-gray-700 leading-relaxed">
                        {selectedProject.description}
                      </p>
                    </div>
                    
                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 mb-3">Gallery</h3>
                      <div className="grid grid-cols-3 gap-3">
                        {selectedProject.images.map((img, index) => (
                          <button
                            key={index}
                            onClick={() => setActiveImageIndex(index)}
                            className={`relative h-24 rounded-lg overflow-hidden border-2 ${
                              index === activeImageIndex
                                ? 'border-blue-600'
                                : 'border-transparent'
                            }`}
                          >
                            <Image
                              src={img}
                              alt={`Thumbnail ${index + 1}`}
                              fill
                              className="object-cover"
                            />
                          </button>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              </motion.div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}